/* Utilities
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.tal {
 text-align: left; }
.tac {
 text-align: center;
 margin-left: auto;
 margin-right: auto; }
.tar {
 text-align: right; }
.taj {
 text-align: justify; }
.mb-1 {
 margin-bottom: -1rem; }

.block {
 display: block; }

.clearfix {
 @include clearfix; }

/* For grids
 ** –––––––––––––––––––––––––––––––––––––––––––––––––– */
.container:after, .row:after {
 content: "";
 display: table;
 clear: both; }
