//.siteFooter
//text-align: center

.siteFooter {
 width: 100%;
 @include position(fixed,null,null,0px,null); }

.footerStrip {
 //width: 100%
 height: 3.6rem;
 @include font(false, false,#4c4c4c,600,false);
 background-color: rgba(92, 180, 189, 1);
 span {
 @include font(false, 1.2rem,false,600,3.6); }
 a {
 @include font(false, false,#4c4c4c,false,false);
 img {
 vertical-align: middle;
 line-height: 36px;
 @include size(3rem);
 display: inline;
 margin: 0 1rem 0.5rem 1rem; }
 &:hover {
 @include font(false, false,#f08dc4,false,false); } } }
