/* Grid life tab
 **
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.mediaGrid {
 margin: 4rem auto;
 max-width: 960px;
 display: flex;
 flex-wrap: wrap;
 justify-content: space-around;
 align-content: space-around;


 .mediaCell {
 flex: 1 1 295px;
 margin: 1rem;
 max-width: 292px;
 border: 1px solid #000000;
 box-shadow: 0 2px 10px darken(#000000, 10%);
 .mediaBox {
 position: relative;
 .ajax {
 @include position(absolute,0px,null,null,0px,);
 width: 100%;
 height: 100%;
 opacity: 0;
 //color: $siteLinkColor
 span {
 @include font('Josefin Sans', 2rem, $text, 600, 1);
 @include cv; } }
 .mask {
 opacity: 0;
 @include position(absolute,0px,0px,0px,0px,);
 text-align: center;
 background: rgba(240, 141, 196, 0.6); }
 &:hover .ajax,
 &:hover .mask {
 opacity: 1;
 transition: opacity 1s; } }

 .mediaCellDesc {
 font-family: 'Handlee';
 margin: 0 auto;
 text-align: center; } } }
