/* Navigation
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

[id^="page"] {
 transition: opacity 2s ease-out;
 opacity: 0;
 height: 0;
 overflow: hidden; }
#page0 {
 opacity: 1;
 height: auto; }

.navigatorWrapper {
 background-color: rgba(92, 180, 189, 1);
 padding: 0 0 vr(1) 0;
 text-align: center;
 .navigatorButtonWrapper {
 display: flex;
 flex-direction: row;
 flex-wrap: nowrap;
 align-items: center;
 justify-content: space-around;
 input {
 display: none; }
 input:checked + label {
 color: #FFFFFF;
 cursor: default; }
 label {
 display: inline-block;
 border: 3px solid transparent;
 cursor: pointer;
 @include font('Handlee', 2rem, $text, 700, false);
 //+typi('th-zero')
 &:hover {
 cursor: pointer;
 color: rgba(240, 141, 196, 1);
 border-bottom: 3px solid rgba(240, 141, 196, 1); } } } }
