// Heading font
@font-face {
 font-family: 'Amatic SC';
 font-style: normal;
 font-weight: 700;
 src: url('data:application/octet-stream;base64,');
 font-display: swap; }

// Banner font
@font-face {
 font-family: 'Handlee';
 font-style: normal;
 font-weight: 400;
 src: local('Handlee Regular'), local('Handlee-Regular'), url(https://fonts.gstatic.com/s/handlee/v8/-F6xfjBsISg9aMakPm3wowtKzig.woff2) format('woff2');
 unicode-range: U+0041-005A, U+0061-007A;
 font-display: swap; }

// Text font
@font-face {
 font-family: 'Josefin Sans';
 font-style: normal;
 font-weight: 400;
 src: local('Josefin Sans Regular'), local('JosefinSans-Regular'), url(https://fonts.gstatic.com/s/josefinsans/v14/Qw3aZQNVED7rKGKxtqIqX5EUDXx4Vn8sig.woff2) format('woff2');
 unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD; }

.heading {
 @include font('Amatic SC', false, false, false, false); }
.subheading {
 @include font('Handlee', false, false, false, false); }
.testo {
 @include font('Josefin Sans', false, false, false, false); }
.fantasy {
 @include font(fantasy, false, false, false, false); }
.cursive {
 @include font(cursive, false, false, false, false); }

h1, h2, h3, h4, h5, h6 {
 margin-top: 0;
 margin-bottom: 3rem;
 @include font(false, false, false, 600, false);
 //hyphens: auto
 //text-align: justify
 //text-transform: uppercase
 //white-space: nowrap
 //word-break: keep-all
 } //word-wrap: break-word

h1 {
 @include font(false, 4.0rem, false, false, 1.2);
 letter-spacing: .5rem;
 white-space: nowrap; }
h2 {
 @include font(false, 3.4rem, false, false, 1.18);
 } //letter-spacing: -.1rem
h3 {
 @include font(false, 2rem, false, false, 1.7);
 letter-spacing: .3rem; }

/*
 *- NOTE: scala aurea
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

@include mediaQ(exsmall) {
 h1 {
 @include font(false, 6.0rem, false, false, 1.1); }
 h2 {
 @include font(false, 5.2rem, false, false, 1.16); }
 //h3
 } //@include font(false, 4.5rem, false, false, 1.15)

p {
 /* from main: +font($systemFF,1.6rem,$text,400,1.6) */
 @include font(false, false, false, false, 1.5);
 margin-top: 0; }

@include mediaQ(exsmall) {
 p {
 @include font(false, 1.8rem, false, false, 1.4); } }

