/* Base styles
 ** Table of contens:
 *
 *- sunset: #e62e68 in background-color: rgba(230, 46, 104, 0.7)
 *- sunshine: #e66e2e
 *- whale: #5CB4BD
 *- flower: #f08dc4
 *- background-color: rgba(38,57,89,0.6) #263959
 *
 *- NOTE: html is set to 62.5% so that all the REM measurements throughout Skeleton
 *- are based on 10px sizing. So basically 1.5rem = 15px :)
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

$grey050: #fafafa;
$grey500: #9e9e9e;
$grey700: #616161;
$white : #ffffff;
$black : #000000;
$clean: #dfdce3;

$text: #4c4c4c;

$flower: rgba(240, 141, 196, 1);
$whale: rgba(92, 180, 189, 1);

.flower {
 color: $flower; }
.whale {
 color: $whale; }

$siteMaxWidth: 860px;

//$systemFF: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif
//$systemFF: Raleway, HelveticaNeue, Helvetica, Arial, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif
$systemFF: sans-serif, serif, monospace, fantasy, cursive;

*,
*::before,
*::after {
 box-sizing: border-box;
 box-sizing: inherit; }

html {
 -webkit-text-size-adjust: 100%;
 -ms-text-size-adjust: 100%;
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
 font-variant-ligatures: common-ligatures;
 -webkit-tap-highlight-color: transparent;
 font-kerning: normal;
 font-family: $systemFF;
 font-size: 62.5%;
 box-sizing: border-box; }

html.no-js {
 body:before {
 position: absolute;
 top: 50%;
 left: 50%;
 transform: translate(-50%, -50%);
 content: "Contact me at rotokyo@icloud.com"; } }

body {
 -webkit-text-size-adjust: 100%;
 -webkit-font-smoothing: antialiased;
 font-kerning: normal;
 font-variant-ligatures: common-ligatures;
 text-rendering: optimizeLegibility;
 overflow-x: hidden;
 /* currently ems cause chrome bug misinterpreting rems on body element */
 @include font($systemFF,1.6rem,$text,400,1.6);
 margin: 0; }

//.fonts-loaded body
//font-family: 'Josefin Sans', $systemFF

/* margins
 *–––––––––––––––––––––––––––––––––––––––––––––––––– */

input, textarea, select, fieldset {
 margin-bottom: 1.5rem; }

pre, blockquote, dl, figure, table, p, ul, ol, form {
 margin-bottom: 2.5rem; }

