a {
 background-color: transparent;
 -webkit-text-decoration-skip: objects;
 @include font(false,false,false,false,false);
 text-decoration: none;
 &:hover {}
 &:focus,
 &:active,
 &:visited {
 @include font(false,false,false,false,false);
 text-decoration: none;
 outline-width: 0; } }

.zamami {
 display: inline-block;
 border: 3px solid transparent;
 @include font('Handlee', 2rem, $text, 700, false);
 //+typi('th-zero')
 &:hover {
 cursor: pointer;
 color: rgba(240, 141, 196, 1);
 border-bottom: 3px solid rgba(240, 141, 196, 1); } }
