/* Grid life tab
 **
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.indexWrapper {
 text-transform: uppercase;
 //width: 750px
 border: 1px solid #000000;
 padding: 5px;
 margin: 4rem 1rem 4rem; }

#one,
#three,
#five,
#seven {
 overflow: hidden; }

#one-left-one {
 font-size: 5rem;
 letter-spacing: .58rem;
 line-height: 5.6rem;
 text-align: center;
 display: block;
 margin: 0 auto;
 } //float: left

#one-right {
 font-size: 12rem;
 line-height: 13rem;
 //letter-spacing: 2rem
 text-align: center;
 display: block;
 //padding-top: 15px
 //float: right
 } //+clearfix

#one-left-two {
 font-size: 2.4rem;
 line-height: 1.9rem;
 letter-spacing: 0.13rem;
 display: block;
 text-align: center;
 } //float: left

#one-left-three {
 font-size: 2.4rem;
 line-height: 3.4rem;
 letter-spacing: 0.37rem;
 display: block;
 text-align: center;
 hyphens: auto;
 } //float: left

#two-row-one {
 padding-top: 0.8rem;
 font-size: 8rem;
 line-height: 8.5rem;
 display: block;
 text-align: center;
 } //float: left

#two-row-two {
 font-size: 3.2rem;
 line-height: 3.8rem;
 letter-spacing: .2px;
 hyphens: auto;
 text-align: center;
 display: block; }

#two-row-three {
 font-size: 2rem;
 line-height: 1.5;
 hyphens: auto;
 text-align: center;
 display: block;
 padding: 2rem 0;
 } //letter-spacing: .15px

#two-row-four {
 font-size: 2.5rem;
 line-height: 1.2;
 hyphens: auto;
 text-align: center;
 display: block;
 letter-spacing: .3px; }

@media screen and (orientation: landscape) and (min-width: 735px) {
 .indexWrapper {
 max-width: 735px;
 margin: 3rem auto 5rem;

 #one-right {
 float: right;
 text-align: right; }
 #one-left-one, #one-left-two, #one-left-three {
 float: left;
 text-align: left;
 font-size: 3.4rem;
 line-height: 1; }
 #one-left-one {
 letter-spacing: 1rem; }
 #two-row-one {
 font-size: 9rem;
 letter-spacing: 0.37rem;
 margin-bottom: 1rem; }
 #two-row-two, #two-row-three {
 font-size: 3rem;
 line-height: 1.5rem; }
 #two-row-four {
 font-size: 2.5rem;
 line-height: 1.2;
 //hyphens: auto
 text-align: left;
 display: block;
 letter-spacing: -.10px; } } }
